#Balance {
    width: 100%;
    display: flex;
    flex-direction: column;
    font-family: Source Han Sans CN;
    font-size: .14rem;


    .balance-top {
        display: flex;
        flex-direction: row;
        align-items: center;
        border-radius: 8px;
        width: 77.945619%;
        background: rgba(255, 255, 255, 0.9);
        height: 1.03rem;
        margin: 0 auto 0.2rem;
        box-shadow: 0 0 .16rem 0 rgb(137 138 228 / 12%);

        .balance-top-mon {
            height: 0.51rem;
            margin-left: 0.3rem;
            display: flex;
            justify-content: space-between;

            .mon-show {
                display: flex;
                flex-direction: column;

                .show-text {
                    font-size: 0.13rem;
                    line-height: 0.28rem;
                    color: #9696A3;
                }

                .show-num {
                    font-size: 0.28rem;
                    font-family: Arial;
                    font-weight: bold;
                    line-height: 0.28rem;
                    color: #040E34;
                }
            }

            .mon-operation {
                display: flex;
                flex-direction: row;
                align-items: flex-end;

                .recharge-btn {
                    margin-left: 0.12rem;
                    font-size: 0.14rem;
                    background: #5C5EE4;
                    border-radius: 4px;
                    color: #FFFFFF;
                }

                .withdrawal-btn {
                    margin-left: 0.08rem;
                    border: 1px solid #5C5EE4;
                    color: #5C5EE4;
                    opacity: 1;
                    border-radius: 4px;
                }
            }
        }

        .balance-top-unmon {
            height: 0.51rem;
            margin-left: 1.8rem;
            display: flex;
            flex-direction: column;

            .show-text {
                font-size: 0.13rem;
                font-weight: 400;
                line-height: 0.28rem;
                color: #9696A3;
            }

            .show-num {
                font-size: 0.28rem;
                font-family: Arial;
                font-weight: bold;
                line-height: 0.28rem;
                color: #040E34;
            }
        }
    }

    .card-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 0.26rem;

        p {
            // width: 0.72rem;
            // height: 0.18rem;
            font-size: .18rem;
            font-weight: 400;
            line-height: 0.18rem;
            color: #040E34;
            margin-left: 0.3rem;
        }
    }

    .card-main {
        margin-top: 0.18rem;


        .ant-table-thead>tr>th {
            background: #F2F2FD;
            color: #9696A3;
            font-size: 0.13rem;
            line-height: 1;
            padding: .13rem 0 .13rem .3rem;
        }

        /* 余额记录类型 */
        .balance-tag {
            padding: .04rem .1rem;
            border-radius: 3px;
            font-size: 13px;
        }

        /* 充值 */
        .Recharge {
            background: rgba(54, 153, 255, 0.12);
            color: rgba(54, 153, 255, 1);
        }

        /* 应用至订单 - 扣费 */
        .Applied {
            background: rgba(249, 150, 0, 0.12);
            color: rgba(249, 150, 0, 1);
        }

        /* 退款 */
        .Refund {
            background: rgba(240, 20, 47, 0.12);
            color: rgba(240, 20, 47, 1);
        }

        /* 提现 */
        .Withdraw {
            background: rgba(61, 213, 152, 0.12);
            color: rgba(61, 213, 152, 1);
        }

        // .ant-table-thead>tr>th {
        //     background: #F2F2FD;
        //     color: #9696A3;
        //     height: 0.13rem;
        //     font-size: 0.13rem;
        //     font-weight: 400;
        // }

        .ant-table-tbody>tr>td {
            padding: 0 0 0 .3rem;
            height: 0.6rem;
            font-size: 0.14rem;
        }

        .ant-table-cell::before {
            height: 0px !important;
        }

        .pay-link {
            width: 0.42rem;
            height: 0.14rem;
            font-size: 0.14rem;
            font-weight: 400;
            line-height: 0px;
            color: #5C5EE4;
            opacity: 1;
        }
    }

    .ant-modal-body {
        padding: .24rem;
    }

    .recharge-modal,
    .withdrawal-modal {
        .modal-title {
            height: 0.18rem;
            font-size: 0.18rem;
            font-weight: 400;
            color: #040E34;
        }

        .modal-form {
            margin-top: 0.32rem;
            width: 100%;
            height: 100%;

            .ant-select-selector,
            .ant-input-number,
            .ant-input,
            .modal-code-input {
                background: #F2F2F7;
            }



            .r-button {
                font-size: 0.13rem;
                color: #5C5EE4;
            }

            .pay-list {
                // width: 100%;
                // margin-left: 15%;
                display: flex;
                justify-content: center;

            }
        }

        .modal-footer {
            display: flex;
            justify-content: center;
            margin-top: .2rem;

            .form-button {
                background: #5C5EE4;
                border-radius: 4px;
            }

            .close {
                border: 1px solid #D9DBE1;
                background: #FFFFFF;
                font-size: 0.14rem;
                font-weight: 400;
                line-height: 0.24rem;
                color: #525666;
                margin-left: 0.08rem;
            }
        }
    }

    @media screen and (max-width: 768px) {

        .balance-top {
            width: 100%;
            margin: 0 0.3rem 0.2rem;
        }
    }
}

.com-content {
    height: auto;
}

// modal 打开时给body添加该类名，modal关闭时移除该类名 解决打开modal时页面滚动造成选择框选项滚动
.body-overflow-hidden {
    overflow: hidden;
}
@primary-color: #5C5EE4;